<script>
	import Shipping from '../../api/shipping'
	import GoodIndent from '../../api/goodIndent'
	import Coupon from '../../api/coupon';
	import {mapMutations} from 'vuex'
	export default {
		data() {
			return {
				maskState: 0, //优惠券面板显示状态
				desc: '', //备注
				payType: 1, //1微信 2支付宝
				couponList: [
					{
						title: '新用户专享优惠券',
						price: 5,
					},
					{
						title: '庆五一发一波优惠券',
						price: 10,
					},
					{
						title: '优惠券优惠券优惠券优惠券',
						price: 15,
					}
				],
				goodList: [],
				addressData: {},
				carriage: 0,
				total: 0,
				outPocket: 0,
				data: {
					indentCommodity: [],
					address: {},
					remark: '',
					carriage: 0,
					user_coupon_id: ''
				},
				order: [],
				maskState: 0,
				couponMoney: 0,
				couponIndex: null
			}
		},
		onLoad(option){
			this.loginCheck()
			this.loadData()
		},
		watch: {
			total(newVal) {
				this.getCouponList(newVal)
			}
		},
		methods: {
			...mapMutations(['loginCheck']),
			//商品数据
			async loadData(){
				this.cartList = []
				this.invalidGood = []
				let cartList =  uni.getStorageSync('dsshopOrderList') || {}
				const that = this
				for(var k in cartList){
					this.order.push({
						id: cartList[k].good_id,
						number: cartList[k].number,
						freight_id: cartList[k].good.freight_id
					})
					cartList[k].checked = true
					cartList[k].loaded = 'loaded'
					if(cartList[k].good_sku){
						cartList[k].good_sku.skus.forEach(item=>{
							if(cartList[k].specification){
								cartList[k].specification+= item.v + ';'
							}else{
								cartList[k].specification = item.v + ';'
							}
							
						})
						cartList[k].specification = cartList[k].specification.substr(0,cartList[k].specification.length-1)
					}
					if(cartList[k].invalid === true){ //失效的商品
						cartList.splice(k,1)
					}
				}
				this.goodList = cartList
				that.data.indentCommodity = cartList
				that.calcTotal()  //计算总价
				that.getOne()
				
				
			},
			//获取默认收货地址
			getOne(){
				const that = this
				Shipping.defaultGet(this.order, function(res){
					that.addressData = res.shipping ? res.shipping : ''
					that.carriage = res.carriage ? res.carriage : 0
					that.outPocketTotal() //实付金额
				})
			},
			numberChange(data) {
				this.number = data.number;
			},
			changePayType(type){
				this.payType = type;
			},
			submit(){
				if(!this.addressData){
					this.$api.msg('请选择地址')
					return false
				}
				this.data.address = this.addressData
				this.data.carriage = this.carriage
				if(this.couponList.length>0){
					this.data.user_coupon_id = this.couponList[this.couponIndex].id
				}
				GoodIndent.create(this.data,function(res){
					uni.removeStorageSync('dsshopOrderList')
					uni.removeStorageSync('dsshopCartList')
					uni.redirectTo({
						url: '/pages/money/pay?id='+res
					})
				})
				
			},
			addAddress(){
				uni.navigateTo({
					url: `/pages/address/address?type=1`
				})
			},
			//地址选择回调
			refreshAddress(item){
				this.addressData = item
			},
			//计算总价
			calcTotal(){
				let list = this.goodList
				let total = 0
				for(var k in list){
					total += list[k].price * list[k].number
				}
				this.total = Number(total.toFixed(2))
			},
			//计算实付金额
			outPocketTotal(){
				let outPocket = 0
				outPocket = outPocket + this.total + this.carriage  - this.couponMoney
				this.outPocket = Number(outPocket.toFixed(2))
			},
			stopPrevent(){},
			//获取可用的优惠券
			getCouponList(money){
				let that = this
				let couponList = []
				let couponMoney = 0 //默认优惠金额
				let couponIndex = null //默认优惠券index
				Coupon.getUserList({
					money: money,
					limit: 100
				}, function(res) {
					res.data.forEach((item,index)=>{
						let data = {
							id: item.id,
							title: item.coupon.name,
							explain: item.coupon.explain,
							endTime: item.failure_time ? item.failure_time.split(' ')[0].replace(/-/g,".") : item.coupon.endtime.split(' ')[0].replace(/-/g,".")
						}
						
						switch(item.coupon.type){
							case 1:
							data.type = '满减优惠券'
							data.price = item.coupon.cost/100
							if(data.price > couponMoney){
								couponMoney = data.price
								couponIndex = index
							}
							
							break
							case 2:
							data.type = '随机优惠券'
							data.price = item.coupon.cost/100
							if(data.price > couponMoney){
								couponMoney = data.price
								couponIndex = index
							}
							
							break
							case 3:
							data.type = '折扣优惠券'
							data.price = that.total * item.coupon.cost/10000
							if(data.price > couponMoney){
								couponMoney = data.price
								couponIndex = index
							}
							break
						}
						if(item.coupon.sill){
							data.sill = '满' + (item.coupon.sill/100) + '可用'
						}else{
							data.sill = '无门槛'
						}
						couponList.push(data)
					})
					that.couponList = couponList
					that.couponMoney = couponMoney
					that.couponIndex = couponIndex
					that.outPocketTotal()
				})
			},
			//显示优惠券面板
			toggleMask(type){
				let timer = type === 'show' ? 10 : 300;
				let	state = type === 'show' ? 1 : 0;
				this.maskState = 2;
				setTimeout(()=>{
					this.maskState = state;
				}, timer)
			},
			// 切换优惠券
			toggleCoupon(index){
				this.couponIndex = index
				this.couponMoney = this.couponList[index].price
				this.outPocketTotal()
				this.toggleMask('hide')
			},
		}
	}
</script>

